import { Component, OnInit, Input, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';
import { Todo } from '../todo';
import { EditDirective } from '../directives/edit.directive';
import  {ItComponent } from '../dynamic/it.component';

@Component({
  selector: 'app-todo-item',
  templateUrl: './todo-item.component.html',
  styleUrls: ['./todo-item.component.css'],
  //inputs:['item']  
})
export class TodoItemComponent implements OnInit {
	@Input() item:Todo;	 
	@Output() todoDelete = new EventEmitter<Todo>();
  @Output() todoTogle = new EventEmitter<Todo>();	
	@ViewChild('itemLbl') itemLabel:HTMLElement;
  @ViewChild(EditDirective) itAnchor: EditDirective;

	edit:boolean = false;

  constructor() { }

	onDelete(todo:Todo){		
		this.todoDelete.emit(todo);
	}
	
	onEdit(item){		
		let label:string = this.itemLabel.textContent;		
	}
	
	onToggle(todo:Todo){
		this.todoTogle.emit(todo);
	}
  ngOnInit() {		
  }

	addIt() {						
			this.edit = true;
			this.itAnchor.createComponent(ItComponent, this.item.name);
			this.itAnchor.destroy.subscribe(((val) => {
				this.item.name = val;				
				this.edit = false;
			}).bind(this));				
	}

}



