import { Input, Component, OnInit, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-it',
  template: '<input class="edit" #edited [(ngModel)]="val" (blur)="onBlur()"/>' 
  //`<input class="edit" [value]="val" #editedtodo >`  
})
export class ItComponent implements OnInit {
  @Input() val: string;
  close = new EventEmitter();

    onBlur() {      
        this.close.emit(this.val);
    }

  constructor() { }
  stopEditing(){

  }
  ngOnInit() {
  }

}



