import {EventEmitter, Directive, ComponentFactoryResolver, ComponentFactory, ComponentRef}  from '@angular/core';
import {ViewContainerRef} from '@angular/core';

import {ItComponent} from '../dynamic/it.component';

@Directive({ 
  selector: '[itemedit]'
})
export class EditDirective {    
    destroy = new EventEmitter();

    constructor(
        private viewContainer: ViewContainerRef,
        private componentFactoryResolver: ComponentFactoryResolver
    ) {}

    createComponent(itComponent: { new(): ItComponent }, val:string): ComponentRef<ItComponent> {
        this.viewContainer.clear();

        let componentFactory = 
          this.componentFactoryResolver.resolveComponentFactory(itComponent);
        let componentRef = this.viewContainer.createComponent(componentFactory);        
        componentRef.instance.val = val;        
        
        componentRef.instance.close.subscribe((val) => {            
            componentRef.destroy();
            this.destroy.emit(val);
        });

        return componentRef;
    }
}



